import { ObjectBase } from "../../engine"
import {
  IResourceDictionary,
  Point,
  SimpleRope
} from "pixi.js"

export default class Arm extends ObjectBase {
  _width
  _height

  ropeLength
  pointCount = 3
  points: Point[] = []

  count = 0

  constructor(resources: IResourceDictionary) {
    super(resources)

    const arm = resources!["taro"].textures!["arm.png"]
    this.zIndex = 8000

    this._width = arm.width
    this._height = arm.height

    this.ropeLength = this._width / this.pointCount
    for (let i = 0; i < this.pointCount; i++) {
      this.points.push(new Point(i * this.ropeLength, 0))
    }

    this.points[this.pointCount - 1].y = 2

    let rope = new SimpleRope(arm, this.points)

    this.addChild(rope)
  }

  tick(delta: number) {
    super.tick(delta)

    this.count += 0.1

    for (let i = 0; i < this.points.length; i++) {
      this.points[i].y =
        (Math.sin(1.075 * this.count) * this._height) / 8
    }
  }
}
