type callback = (() => void) | null

/**
 * @class
 */
export class Key {
  /**
   *
   * @type {boolean}
   * @private
   */
  private _isDown = false
  /**
   *
   * @type {boolean}
   * @private
   */
  private _isUp = true

  // events
  /**
   *
   * @type {KeyboardEvent}
   */
  public _pressEvent: callback = null
  /**
   *
   * @type {KeyboardEvent}
   */
  public _releaseEvent: callback = null

  constructor(
    private readonly _key: string,
    private readonly _ignoreKeyup: boolean,
    pressEvent?: callback,
    releaseEvent?: callback
  ) {
    this._downHandler = this._downHandler.bind(this)
    this._upHandler = this._upHandler.bind(this)

    this._pressEvent = pressEvent || null
    this._releaseEvent = releaseEvent || null

    //Attach event listeners
    window.addEventListener(
      "keydown",
      this._downHandler,
      false
    )
    window.addEventListener("keyup", this._upHandler, false)
  }

  destroy() {
    window.removeEventListener(
      "keydown",
      this._downHandler,
      false
    )
    window.removeEventListener(
      "keyup",
      this._upHandler,
      false
    )
  }

  get isDown() {
    return this._isDown
  }

  get releaseEvent() {
    return this._releaseEvent
  }

  get pressEvent() {
    return this._pressEvent
  }

  /**
   *
   * @param event
   * @private
   */
  private _downHandler(event: KeyboardEvent) {
    if (event.key === this._key) {
      event.preventDefault()

      // if the key is up and we have a press handler
      if (this._isUp && this.pressEvent) {
        this.pressEvent()
      }

      this._isDown = true
      this._isUp = false
    }
  }

  /**
   *
   * @param event
   * @private
   */
  private _upHandler(event: KeyboardEvent) {
    if (event.key === this._key) {
      event.preventDefault()

      // if the key is down and we have a press handler
      if (this._isDown && this.releaseEvent) {
        this.releaseEvent()
      }

      this._isDown = false
      this._isUp = true
    }
  }
}
