import { ActorBase } from "./actor-base"
import { IResourceDictionary } from "pixi.js"
import { ObjectBase, WeaponBase } from "../objects"

/**
 *
 */
export class PlayerBase extends ActorBase {
  protected _name: string = "Unplayed Namer"
  protected _weapon: WeaponBase
  protected _arm: ObjectBase

  get weapon(): WeaponBase {
    return this._weapon
  }

  get arm(): ObjectBase {
    return this._arm
  }

  get playerName() {
    return this._name
  }

  constructor(
    resources: IResourceDictionary,
    skin: string,
    speed: number
  ) {
    super(resources, skin, speed)

    this._arm = new ObjectBase(resources)
    this._weapon = new WeaponBase(resources, this, 0, 0)
  }
}
